.box_wrapper{
    margin-top: 10%;
    width:100%;
    height: 5%;
    padding: 12px 20px;
    display:flex;
    border: 3px solid grey;
    transition: border 0.4s;
    align-items: center;
    vertical-align:middle;
}
.box_wrapper:hover{
    border: 3px solid lightseagreen;
}
.box_wrapper2{
    margin-top: 0%;
    width:100%;
    height: 5%;
    padding: 12px 20px;
    display:flex;
    border-left: 3px solid grey;
    border-right:3px solid grey;
    border-bottom: 3px solid grey;
    border-top: none;
    transition: border 0.4s;
    align-items: center;
    vertical-align:middle;
}
.box_wrapper2:hover{
    border: 3px solid lightseagreen;
}
